import React, { useEffect } from "react";

function About(props) {
  useEffect(() => {
    document.title = "Draftbox Design / About";
  }, []);

  return (
    <div className="about">
      <h3>
        <img
          src="../images/minibox.png"
          alt=""
          style={{ height: "25px", marginRight: "5px" }}
        />
        &nbsp;About
      </h3>

      <div>
        <i className="fa fa-comment"></i>
        <div className="quote">
          <div>
            <i>"Architecture must work with nature."</i>
            <div>- Raffiel Fetalvero</div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default About;
