import React, { useEffect, useState } from "react";

function Portfolio(props) {
  const [images, setImages] = useState([]);
  const [imgIndex, setImgIndex] = useState(0);
  const [imgReady, setImageReady] = useState(false);
  const [imgsToShow, setImgsToShow] = useState([images.length - 1, 0, 1]);

  async function fetchUrl(url) {
    const response = await fetch(url);
    const images = await response.json(); //.then(data => data.images);

    // const images = [
    //   {
    //     filename: "house6.jpg",
    //     description: "",
    //   },
    //   {
    //     filename: "house1.jpg",
    //     description: "",
    //   },
    //   {
    //     filename: "house2.jpg",
    //     description: "",
    //   },
    //   {
    //     filename: "house3.jpg",
    //     description: "",
    //   },
    //   {
    //     filename: "house4.jpg",
    //     description: "",
    //   },
    //   {
    //     filename: "house5.jpg",
    //     description: "",
    //   },
    //   {
    //     filename: "building1.jpg",
    //     description: "",
    //   },
    //   {
    //     filename: "building2.jpg",
    //     description: "",
    //   },
    //   {
    //     filename: "building3.jpg",
    //     description: "",
    //   },
    //   {
    //     filename: "building4.jpg",
    //     description: "",
    //   },
    //   {
    //     filename: "building5.jpg",
    //     description: "",
    //   },
    //   {
    //     filename: "building6.jpg",
    //     description: "",
    //   },
    //   {
    //     filename: "building7.jpg",
    //     description: "",
    //   },
    //   {
    //     filename: "building8.jpg",
    //     description: "",
    //   },
    //   {
    //     filename: "port1.jpg",
    //     description: "",
    //   },
    //   {
    //     filename: "port2.jpg",
    //     description: "",
    //   },
    //   {
    //     filename: "port3.jpg",
    //     description: "",
    //   },
    //   {
    //     filename: "port4.jpg",
    //     description: "",
    //   },
    // ];

    setImages([...images]);
    setImgsToShow([images.length - 1, 0, 1]);

    //console.log(images.length);
  }

  useEffect(() => {
    fetchUrl("index.json"); //"portfolio/_data.json");
    // fetch("portfolio/_data.json")
    //   .then((res) => res.json())
    //   .then((result) => {
    //     //let imgs = result.images || [];
    //     console.log(result);
    //     //setImages(result.images);
    //     //setImgsToShow([result.length - 1, 0, 1]);
    //   });
  }, []);

  // const buildings = [1, 2, 3, 4, 5, 6, 7, 8];
  // const houses = [6, 1, 2, 3, 4, 5];
  // const ports = [1, 2, 3, 4, 5];

  // let images = [];
  // houses.forEach((i) => {
  //   images.push(`house${i}.jpg`);
  // });
  // buildings.forEach((i) => {
  //   images.push(`building${i}.jpg`);
  // });
  // ports.forEach((i) => {
  //   images.push(`port${i}.jpg`);
  // });

  useEffect(() => {
    document.title = "Draftbox Design / Porfolio";

    if (!images || images.length === 0) {
      return;
    }

    const timer = setInterval(() => {
      if (!imgReady) {
        return;
      }
      moveNext();
    }, 8000);

    return () => clearInterval(timer);
  }); //[imgIndex, imgsToShow, imgReady, images.length]);

  function updateImagesToShow(idx) {
    setImgsToShow([
      idx === 0 ? images.length - 1 : idx - 1,
      idx,
      idx === images.length - 1 ? 0 : idx + 1,
    ]);
  }

  function moveNext() {
    let newIdx = imgIndex + 1;
    if (newIdx === images.length) {
      newIdx = 0;
    }
    setImageReady(false);
    setImgIndex(newIdx);
    updateImagesToShow(newIdx);
  }

  function movePrev() {
    let newIdx = imgIndex - 1;
    if (newIdx === -1) {
      newIdx = images.length - 1;
    }
    setImageReady(false);
    setImgIndex(newIdx);
    updateImagesToShow(newIdx);
  }

  function imageReady() {
    setImageReady(true);
  }

  return (
    <div className="portfolio">
      <h3 style={{ zIndex: 1 }}>
        <img
          src="../images/minibox.png"
          alt=""
          style={{ height: "25px", marginRight: "5px" }}
        />
        &nbsp;Portfolio
      </h3>

      <div className={"imgPreview " + (imgReady ? "imgReady" : "")}>
        <div style={{ zIndex: 1 }} className="imgNav">
          <div onClick={movePrev}>
            <i className="fa fa-chevron-left"></i>
          </div>
          <div className={imgReady ? "imgReady" : ""}>
            {imgReady ? "" : "loading..."}
          </div>
          <div onClick={moveNext}>
            <i className="fa fa-chevron-right"></i>
          </div>
        </div>

        {/* <div className="imagePlay">
          <i className="fa fa-pause"></i>
        </div> */}

        <div className="imgsToShow">
          {imgsToShow.map((i) => {
            if (!images[i]) {
              return "";
            }
            return (
              <div
                key={i}
                style={{
                  backgroundImage: `url(../portfolio/${images[i].filename})`,
                }}
              >
                {/* <section></section> */}
              </div>
            );
          })}
        </div>
        {images[imgIndex] && (
          <>
            <img
              style={{ height: "10px", opacity: 0 }}
              alt={`${images[imgIndex].filename}`}
              src={`../portfolio/${images[imgIndex].filename}`}
              onLoad={() => imageReady()}
            ></img>
          </>
        )}
      </div>
    </div>
  );
}

export default Portfolio;
