import React, { useState } from "react";
import { NavLink } from "react-router-dom";

function Continer({ children }) {
  const [openMenu, setOpenMenu] = useState(false);

  function toggleMenu() {
    setOpenMenu(!openMenu);
  }

  function changePage(page = "Home") {
    setOpenMenu(false);
    document.title = "Draftbox Design" + (page === "Home" ? "" : ` / ${page}`);
  }

  return (
    <>
      <div
        id="container"
        className={"container " + (openMenu ? "openMenu" : "")}
      >
        <div className={openMenu ? "openMenu" : ""}>
          <img
            src="../images/draftbox.svg"
            alt="Draftbox Logo"
            style={{ width: "100px" }}
          />
          <div>
            <ul>
              <li onClick={() => changePage("Home")}>
                <NavLink exact activeClassName="active" to="/">
                  Home
                </NavLink>
              </li>
              <li onClick={() => changePage("Portfolio")}>
                <NavLink exact activeClassName="active" to="/portfolio">
                  Portfolio
                </NavLink>
              </li>
              <li onClick={() => changePage("Contact")}>
                <NavLink exact activeClassName="active" to="/contact">
                  Contact
                </NavLink>
              </li>
              <li onClick={() => changePage("About")}>
                <NavLink exact activeClassName="active" to="/about">
                  About
                </NavLink>
              </li>
            </ul>
          </div>

          <div>
            <ul style={{ padding: "6px 0" }}></ul>
          </div>

          <div>
            <ul style={{ padding: "2px 0" }}></ul>
          </div>
        </div>

        <div className={openMenu ? "openMenu" : ""}>
          {children}

          <section className="footer">Draftbox Design &copy; 2021 - v1.0</section>

          <section
            onClick={toggleMenu}
            className={"mobileOverlay " + (openMenu ? "openMenu" : "")}
          ></section>
        </div>
      </div>

      <div id="svgLogoAnim">
        <div id="svgImgs">
          <img id="petal3" src="../images/petal3.svg" alt="" />
          <img id="petal2" src="../images/petal2.svg" alt="" />
          <img id="petal1" src="../images/petal1.svg" alt="" />
          <img id="box3" src="../images/box3.svg" alt="" />
          <img id="box2" src="../images/box2.svg" alt="" />
          <img id="box1" src="../images/box1.svg" alt="" />
          <div id="logoContainer">
            <div id="logoLayer1">
              <div id="logoLayer2"></div>
            </div>
          </div>
        </div>
      </div>

      <section
        onClick={toggleMenu}
        className={"mobileMenu " + (openMenu ? "openMenu" : "")}
      >
        <i className="fa fa-bars"></i>
      </section>
    </>
  );
}

export default Continer;
