import React, { useEffect } from "react";
import { Link } from "react-router-dom";

function NotFound(props) {
  useEffect(() => {
    document.title = "Draftbox Design / Not Found";
  }, []);

  return (
    <div className="notfound">
      <h3>
        <img
          src="../images/minibox.png"
          alt=""
          style={{ height: "25px", marginRight: "5px" }}
        />
        &nbsp;Page Not Found
      </h3>

      <div>
        <span>
          <i className="fa fa-ban"></i>
        </span>
        <span>Error 404 - there's nothing in here.</span>
      </div>
      <div>
        <span>
          <i className="fa fa-sitemap"></i>
        </span>
        <span>
          <Link to="/">Home</Link> • <Link to="/portfolio">Portfolio</Link> •{" "}
          <Link to="/contact">Contact</Link> • <Link to="/about">About</Link>
        </span>
      </div>
    </div>
  );
}

export default NotFound;
